<!-- 登录页 -->
<template>
    <div class="page-login">
        <div class="login-bg-video login-box" :style="{backgroundImage:'url(' + imgs.bg + ')'}">
            <video  autoplay loop muted>
                <source :src="imgs.video" type="video/mp4">
            </video>
        </div>
        <div class="login-box login-form">
            <img class="login-logo" :src="imgs.logo" alt="logo">
            <img class="login-sponsor" :src="imgs.sponsor" alt="sponsor">
            <div class="login-content">
                <div class="login-item">
                    <div class="input-icon">
                        <Icon class="input-icon-item" :icon="'iconzhanghao'" />
                    </div>
                    <input type="text" class="input-item" v-model="form.username">
                </div>
                <div class="login-item">
                    <div class="input-icon">
                        <Icon class="input-icon-item" :icon="'iconmima'" />
                    </div>
                    <input :type="showPass ? 'text' : 'password'" class="input-item" v-model="form.password">
                    <div class="input-tail" @click="showPass = !showPass">
                        <Icon v-show="showPass" class="input-icon-item" :icon="'iconxianshimima'" />
                        <Icon v-show="!showPass" class="input-icon-item" :icon="'iconyincangmima'" />
                    </div>
                </div>
                <div class="login-else">
                    <label @click="remember = !remember">
                        <Icon v-show="!remember" class="login-icon" :icon="'iconcheckbox_uncheck'" />
                        <Icon v-show="remember" class="login-icon icon-checked" :icon="'iconcheckbox_checked'" />
                        <span class="login-link">记住密码</span>
                    </label>
                    <span class="login-link">忘记密码？</span>
                </div>
                <div @click="jump('Home')" class="login-item login-btn" :class="(form.username && form.password) ? '' : 'unuse-btn'">登录</div>
                <div class="login-else">
                    <span class="login-link link-regist" @click="jump('Register')">注册新账号</span>
                    <span class="login-link">先去逛逛</span>
                </div>
                <div class="login-serve">
                    <img :src="imgs.service" alt="service">
                    <span class="login-link">联系客服</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Icon from '../components/Icon'
import bg from '../assets/bg.jpg'
import video from '../assets/video.mp4'
import logo from '../assets/login_logo.svg'
import sponsor from '../assets/sponsor.png'
import service from '../assets/icon-online-service.svg'

export default {
    data () {
        return {
            imgs: {
                bg: bg,
                video: video,
                logo: logo,
                sponsor: sponsor,
                service: service
            },
            remember: false,
            showPass: true,
            form: {
                username: '',
                password: ''
            }
        }
    },
    components: {
        Icon
    },
    methods: {
        jump (urlName) {
            this.$router.push({
                name: urlName
            })
        }
    }
}
</script>

<style scoped>
.page-login {
    position: relative;
    overflow: hidden;
    width: 100vw;
    height: 100vh;
}
.login-box {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.login-bg-video {
    z-index: 1;
    background-size: cover;
}
.login-form {
    padding-top: calc(50vh - 200px);
}
.login-content {
    width: 300px;
    margin: 0 auto;
    text-align: center;
}
.login-item {
    height: 40px;
    line-height: 44px;
    display: flex;
    width: 100%;
    border-radius: 20px;
    overflow: hidden;
    -webkit-box-shadow: inset 0 1px 15px 0 #fff;
    box-shadow: inset 0 1px 15px 0 #fff;
    background-color: rgba(255, 255, 255, .5);
    margin: 20px 0 0 ;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}
.login-logo {
    width: 150px;
}
.login-sponsor {
    width: 340px;
    margin: 15px 0 0 ;
}
.login-else {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0 0;
}
.login-link {
    cursor: pointer;
    color: #fff;
    user-select: none;
}
.login-icon {
    font-size: 18px;
    margin-right: 2px;
    color: #fff;
    position: relative;
    top: 1px;
}
.icon-checked {
    color: #d2b79c;
}
.link-regist {
    color: #d2b79c;
}
.login-serve {
    margin-top: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    user-select: none;
}
.login-serve img {
    margin-right: 5px;
}
.login-btn {
    background-color: #d2b79c;
    text-align: center;
    color: #fff;
    justify-content: center;
    box-shadow: none;
    cursor: pointer;
}
.unuse-btn {
    cursor: not-allowed!important;
    opacity: .4!important;
    user-select: none;
}
.input-icon {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    overflow: hidden;
    background-color: rgba(255, 255, 255, .5);
    margin: 0 10px 0 10px;
    color: #d2b79c;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
}
.input-item {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    height: 100%;
    line-height: 100%;
    border: none;
    outline: none;
    padding: 10px 55px 10px 0;
    font-size: 14px;
    color: #3e4a70;
    opacity: .9;
    font-family: DinProMedium!important;
    background-color: rgba(255, 255, 255, 0);
}
.input-tail {
    margin: 0 10px 0 10px;
    color: #666;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    cursor: pointer;
}
</style>